.cardsnoticias{
  display: flex;
  flex-direction: row;
  gap:30px;
  justify-content: center;
  width: 100%;
  flex-wrap: wrap;

}


.cardnoticia-link {
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: space-between;
  text-decoration: none;
  color: var(--text);
  text-align: center;
}
.cardnoticia-imagem {
  width: 100%;
  object-fit: fill;
  width: 100%;
  min-height: 10rem;
}


.cardnoticia-titulo{
  margin: 0;
  text-align: center;
  width: 100%;
  align-items: center;
}
.cardnoticia-descricao{
  margin: 0;
  text-align: left;
}
.cardnoticiatext > div:last-child {
  align-items: center;
  justify-content: left;

}

.cardnoticia {
  display: flex;
  flex-direction: column;
  max-width: calc(33.33% - 40px);
  border-radius: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  position: relative;
  overflow: hidden;
}

.cardnoticia:hover::after {
  content: "";
  position: absolute;
  width: 100%;
  bottom: -2px;
  left: 0;
  height: 5px;
  background-color: var(--roxoprimary); /* Cor do hover e ativo para home */
  transition: all 0.3s ease;
}


.cardnoticiatext {
  flex:1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5% 10% 5% 10%;
  flex-wrap: wrap;
  gap: 20px;
  text-align: center;
}

.cardnoticiatext > h1 {
  background-color: #3498db; /* Cor azul */
  font-size: 30px;
}

.cardnoticiatext > a:first-child {
  width: 100%;
  font-weight: var(--semi-bold);
}

.cardnoticiatext > div {
  justify-content: center;
  margin: 0;
  text-align: center;
  width: 100%;
  align-items: center;
}





.headerPaginaNoticia .tituloPaginaNoticia {
  font-size: 30px; 
  color: #000000; 
  font-weight: bold;
  margin-top: 50px; 
  margin-bottom: 5px; 
}

.InformacoesDebaixoImagem { 
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 10%;
  text-align: justify;
}

.TrocarNoticia {
  background-color: #FBEAD6;
  height: 100px;
  display: flex;
  align-items: center; 
  padding: 0 2%;
  margin-bottom: -50px;
}

.DivImagemNoticia {
  display: flex;
  flex: 1;
  justify-content: center;
  width: 100%;
  height: auto;
  padding: 0 10%;
}

.DivImagemNoticia img {
  width: 100%;          
  max-height: 300px;    
  object-fit: cover;    
}

@media (max-width: 930px) {
  .cardnoticia {
    display: flex;
    flex-direction: column;
    max-width: calc(50% - 30px);
    border-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  
  }
}

@media (max-width: 630px) {
  .cardsnoticias{
    flex-direction: column;
  }
  .cardnoticia {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    border-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  
  }
  .cardnoticia-imagem {
    object-fit: fill;
    width: 100%;
    min-height: 10rem;
  }
}

